<template>
  <div class="login-wrapper">
    <div class="login-container">
      <div class="login-card">
        <h4 class="login-title">Staff Login</h4>

        
    <form @submit.prevent="loginStaff">
          <div class="form-group">
            <label class="form-label">Enter Staff Name</label>
            <input
              v-model="name"
          type="text"
          class="form-control"
          required
          placeholder="Enter staff name"
            />
          </div>
          <div class="form-group">
            <label class="form-label">Enter Passcode</label>
            <input
              v-model="passcode"
          type="passcode"
          class="form-control"
          required
          placeholder="Enter passcode"
            />
          </div>
         
          <button type="submit" class="btn-primary">Log in</button>
        </form>
      </div>
    </div>
  </div>
</template>
 



<script>
import axios from "axios";

export default {
  data() {
    return {
      name: "",
      passcode: "",
    };
  },
  methods: {
    async loginStaff() {
      try {

        const res = await axios.post(
          "https://event-ticket-qa70.onrender.com/api/staff/login",
          { name: this.name, passcode: this.passcode },
        );

        if (res.data.success) {
          localStorage.setItem("staffToken", res.data.token);
          alert("Staff login successful!");
          this.$router.push("/scanner");
        } else {
          alert(res.data.message);
        }
      } catch (err) {
        alert(err.response?.data?.message || "Login failed");
      }
    },
  },
};
</script>


<style scoped>
.login-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
}

.login-container {
  max-width: 400px;
  width: 100%;
}

.login-card {
  background-color: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.login-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-weight: bold;
}

.form-group {
  margin-bottom: 1rem;
}

.form-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid #ccc;
  border-radius: 50px;
  font-size: 1rem;
}

.text-end {
  text-align: right;
  margin-bottom: 1rem;
}

.link-muted {
  color: #777;
  text-decoration: none;
  font-size: 0.9rem;
}

.link-teal {
  color: teal;
  font-weight: bold;
  text-decoration: none;
}

.btn-primary {
  width: 100%;
  background-color: #f4a213;
  color: white;
  padding: 0.75rem;
  border: none;
  border-radius: 50px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-primary:hover {
  background-color: #006d6d;
}

.auth-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.btn-dark,
.btn-google {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.btn-dark {
  background-color: #f4a213;
  color: white;
}

.btn-google {
  background-color: #fff;
  color: #444;
  border: 1px solid #ccc;
}

.google-icon {
  background: #fbbc05;
  color: white;
  border-radius: 50%;
  padding: 0.3rem 0.6rem;
  font-weight: bold;
}
</style>